.toggle {
  display: flex;
  justify-content: space-between;
  border: 3px solid var(--rogerBlue);
  border-radius: 1rem;
  position: relative;
  padding: 2px;
  cursor: pointer;
  
}
.toggle > * {
  width: 1rem;
  height: 1rem;
  color: var(--rogerBlue);
}
.t-button {
  border-radius: 100%;
  background: var(--rogerBlue);
  position: absolute;
}
@media screen and (max-width:480px) {
  .toggle{
    padding: 1px;
  }
}
