.Card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background-color: rgba(255,255,255,0.26);
    border: 7px solid var(--rogerBlue);
    box-shadow: var(--rogerBlue);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}
.Card span:nth-of-type(2){
    color: var(--gray);
    font-size: 16px;
}
.Card>img{
    transform: scale(0.6);
    margin-bottom: -2rem;
}
.c-button{
    background-color: #ffffff;
    box-shadow: 0px 19px 60px rgb(0,0,0,0.08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: #5290fd;
    cursor: pointer;
}

@media screen and (max-width:480px) {
    .Card{
        width: auto;
        /* margin: auto; */
        margin-left: -20px;
    }
}